.social {
  margin:0;
  padding:0;
  background: #dedede;
}

.ul {
  position:absolute;
  
  transform: translate(65%, 65%);
  margin:0;
  padding:0;
  display:flex;
}

.ul .li {
  list-style: none;
}

.ul .li .a {
  position: relative;
  width:80%;
  height:80%;
  display:block;
  text-align:center;
  margin:0 10px;
  border-radius: 50%;
  padding: 6px;
  box-sizing: border-box;
  text-decoration:none;
  box-shadow: 0 10px 15px rgba(0,0,0,0.3);
  background: linear-gradient(0deg, #ddd, #fff);
  transition: .5s;
}

.ul .li .a:hover {
  box-shadow: 0 2px 5px rgba(0,0,0,0.3);
}

.ul .li .a .fab {
  width: 80%;
  height:80%;
  display:block;
  background: linear-gradient(0deg, #fff, #ddd);
  border-radius: 50%;
  line-height: calc(60px - 12px);
  font-size:24px;
  color: #262626;
  transition: .5s;
}

.ul .li:nth-child(1) .a:hover .fab {
  color: #3b5998;
}

.ul .li:nth-child(2) .a:hover .fab {
  color: #00aced;
}

.ul .li:nth-child(3) .a:hover .fab {
  color: #dd4b39;
}

.ul .li:nth-child(4) .a:hover .fab {
  color: #007bb6;
}

.ul .li:nth-child(5) .a:hover .fab {
  color: #e4405f;
}