.c {
    display: flex;
    width: 100%;
    padding: 4% 2%;
    box-sizing: border-box;
    height: 100vh;
  }
  
  .box {
    height: 90%;
    flex: 1 1;
    overflow: hidden;
    -webkit-transition: .5s;
    transition: .5s;
    margin: 0 2%;
    box-shadow: 0 20px 30px rgba(0,0,0,.1);
    line-height: 0;
    background-image: -webkit-gradient(linear, left bottom, left top, from(#c4c7c4), color-stop(52%, #dcdddf), to(#ebebeb));
    background-image: -webkit-linear-gradient(bottom, #c4c7c4 0%, #dcdddf 52%, #ebebeb 100%);
    background-image: linear-gradient(to top, #c4c7c4 0%, #dcdddf 52%, #ebebeb 100%);
  }
  
  .box > img {
    width: 100%;
    height: calc(80% );
    object-fit: cover; 
    -webkit-transition: .5s; 
    transition: .5s;
  }
  
  .box > span {
    font-size: 3.8vh;
    display: block;
    text-align: center;
    height: 10vh;
    line-height: 2.6;
  }
  
.box:hover { flex: 40% 1; }
  .box:hover > img {
    width: 90%;
    height: 80%;
   -webkit-transform: scale(1);
           transform: scale(1);
   -webkit-transition-duration: 2s;
           transition-duration: 2s;
   -webkit-transition-delay: 100ms;
           transition-delay: 100ms;
   
  }
  .box:hover{
    background-image: -webkit-gradient(linear, left top, right top, from(#c1c161), color-stop(0%, #c1c161), to(#d4d4b1));
    background-image: -webkit-linear-gradient(left, #c1c161 0%, #c1c161 0%, #d4d4b1 100%);
    background-image: linear-gradient(to right, #c1c161 0%, #c1c161 0%, #d4d4b1 100%);
  }
 
  .loader {
    width: 20em;
    height: 20em;
    font-size: 10px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 90%;
    left: 40%;
}

.loader .face {
    position: absolute;
    border-radius: 50%;
    border-style: solid;
    -webkit-animation: animate 3s linear infinite;
            animation: animate 3s linear infinite;
}

.loader .face:nth-child(1) {
    width: 100%;
    height: 100%;
    color:hotpink;
    border-color: currentColor transparent transparent currentColor;
    border-width: 0.2em 0.2em 0em 0em;
    --deg: -45deg;
    -webkit-animation-direction: normal;
            animation-direction: normal;
}

.loader .face:nth-child(2) {
    width: 70%;
    height: 70%;
    color: rgb(31, 99, 245);
    border-color: currentColor currentColor transparent transparent;
    border-width: 0.2em 0em 0em 0.2em;
    --deg: -135deg;
    animation-direction: reverse;
}

.loader .face .circle {
    position: absolute;
    width: 50%;
    height: 0.1em;
    top: 67%;
    left: 20%;
    background-color: transparent;
    -webkit-transform: rotate(var(--deg));
            transform: rotate(var(--deg));
    -webkit-transform-origin: left;
            transform-origin: left;
}

.loader .face .circle::before {
    position: absolute;
    top: -0.5em;
    right: -0.5em;
    content: '';
    width: 1em;
    height: 1em;
    background-color: currentColor;
    border-radius: 50%;
    box-shadow: 0 0 2em,
                0 0 4em,
                0 0 6em,
                0 0 8em,
                0 0 10em,
                0 0 0 0.5em rgba(255, 255, 0, 0.1);
}

@-webkit-keyframes animate {
    to {
        -webkit-transform: rotate(1turn);
                transform: rotate(1turn);
    }
}

@keyframes animate {
    to {
        -webkit-transform: rotate(1turn);
                transform: rotate(1turn);
    }
}
.loader .face:nth-child(1):hover{
  color:orchid;
}

