.social {
  margin:0;
  padding:0;
  background: #dedede;
}

.ul {
  position:absolute;
  
  -webkit-transform: translate(65%, 65%);
  
          transform: translate(65%, 65%);
  margin:0;
  padding:0;
  display:flex;
}

.ul .li {
  list-style: none;
}

.ul .li .a {
  position: relative;
  width:80%;
  height:80%;
  display:block;
  text-align:center;
  margin:0 10px;
  border-radius: 50%;
  padding: 6px;
  box-sizing: border-box;
  text-decoration:none;
  box-shadow: 0 10px 15px rgba(0,0,0,0.3);
  background: -webkit-gradient(linear, left bottom, left top, from(#ddd), to(#fff));
  background: -webkit-linear-gradient(bottom, #ddd, #fff);
  background: linear-gradient(0deg, #ddd, #fff);
  -webkit-transition: .5s;
  transition: .5s;
}

.ul .li .a:hover {
  box-shadow: 0 2px 5px rgba(0,0,0,0.3);
}

.ul .li .a .fab {
  width: 80%;
  height:80%;
  display:block;
  background: -webkit-gradient(linear, left bottom, left top, from(#fff), to(#ddd));
  background: -webkit-linear-gradient(bottom, #fff, #ddd);
  background: linear-gradient(0deg, #fff, #ddd);
  border-radius: 50%;
  line-height: calc(60px - 12px);
  font-size:24px;
  color: #262626;
  -webkit-transition: .5s;
  transition: .5s;
}

.ul .li:nth-child(1) .a:hover .fab {
  color: #3b5998;
}

.ul .li:nth-child(2) .a:hover .fab {
  color: #00aced;
}

.ul .li:nth-child(3) .a:hover .fab {
  color: #dd4b39;
}

.ul .li:nth-child(4) .a:hover .fab {
  color: #007bb6;
}

.ul .li:nth-child(5) .a:hover .fab {
  color: #e4405f;
}
.heartrate {
    height: 100px;
    width: 200px;
    overflow: hidden;
    position: absolute;
   top:-3%;
    margin: auto;
  }
  .heartrate:after {
    content: '';
    display: block;
    background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 200px 100px" enable-background="new 0 0 200px 100px" xml:space="preserve"><polyline fill="none" stroke-width="3px" stroke="white" points="2.4,58.7 70.8,58.7 76.1,46.2 81.1,58.7 89.9,58.7 93.8,66.5 102.8,22.7 110.6,78.7 115.3,58.7 126.4,58.7 134.4,54.7 142.4,58.7 197.8,58.7 "/></svg>') 0 0 no-repeat;
    width: 100%;
    
    height: 90%;
    position: absolute;
    -webkit-animation: 2s pulse linear infinite;
    animation: 2s pulse linear infinite;
      clip: rect(0, 0, 100px, 0);
  }
  .heartrate:before {
    content: '';
    position: absolute;
    z-index: -1;
    left: 2px;
    right: 2px;
    bottom: 0;
    top: 16px;
    margin: auto;
    height: 3px;
  
  }
  
  
  @-webkit-keyframes pulse {
    0% {
      clip: rect(0, 0, 100px, 0);
      opacity: 0.4;
    }
    4% {
      clip: rect(0, 66.66667px, 100px, 0);
      opacity: 0.6;
    }
    15% {
      clip: rect(0, 133.33333px, 100px, 0);
      opacity: 0.8;
    }
    20% {
      clip: rect(0, 300px, 100px, 0);
       opacity: 1;
    }
  
    80% {
      clip: rect(0, 300px, 100px, 0);
       opacity:0;
    }
  
    90% {
      clip: rect(0, 300px, 100px, 0);
      opacity: 0;
    }
  
    100% {
      clip: rect(0, 300px, 100px, 0);
      opacity:0;
   }
  }
  @keyframes pulse {
    0% {
      clip: rect(0, 0, 100px, 0);
    }
    4% {
      clip: rect(0, 66.66667px, 100px, 0);
    }
    15% {
      clip: rect(0, 133.33333px, 100px, 0);
    }
    20% {
      clip: rect(0, 300px, 100px, 0);
       opacity:1;
    }
  
    80% {
      clip: rect(0, 300px, 100px, 0);
       opacity:0;
    }
  
    90% {
      opacity: 0;
    }
  
    100% {
      clip: rect(0, 300px, 100px, 0);
      opacity:0;
   }
    
  }
  
